import React, { useCallback, useContext, useState } from 'react';
import osmeticsList from '../components/osmetics-list/osmeticsList';
import styles from '../components/osmetics-list/CosmeticsList.module.css';
import Categories from '../components/categories/Categories';
import Sort from '../components/sort/Sort';
import { SearchContext } from '../providers/SearchProvider';
import { useGetCosmeticsQuery } from '../store/api/api';


const Catalogosmetics = () => {
    const [categoryIndex, setCategoryIndex] = useState(0);
    const [sortType, setSortType] = useState({
        name: '', 
        sortProperty: 'rating' 
    });

    const sort = sortType.sortProperty.replace('-', '');
    const order = sortType.sortProperty.includes('-') ?  'desc' : 'asc';
    const category = categoryIndex > 0 ? `?category=${categoryIndex}` : '?_category=0';
    
    const { searchValue } = useContext(SearchContext);
 
    const { data, isLoading}  = useGetCosmeticsQuery({
        category, 
        sort, 
        order,
        searchValue
    });

    const onChangeCategory = useCallback((i) => setCategoryIndex(i), [])

    return (
        <div className={styles.catalog}>
            <h2> </h2>
            <div className={styles.row}>
                <Categories 
                    category={categoryIndex} 
                    onClickCategory={onChangeCategory} 
                />
                <div className={styles.column}>
                    {data && (
                        <Sort 
                            sort={sortType} 
                            onChangeSort={(i) => setSortType(i)} 
                        />  
                    )}
                    <osmeticsList 
                        isLoading={isLoading} 
                        data={data}
                    />
                </div>   
            </div>
        </div>
    )
}

export default Catalogosmetics;
