import React from 'react';
import styles from '../components/osmetics-list/CosmeticsList.module.css';
import CosmeticsItem from '../components/cosmetics-item/CosmeticsItem';
import { NavLink } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { selectFavorites } from '../store/favorites/favorites.slice';

const Favorites = () => {
    const favorites = useSelector(selectFavorites);

    return (
        <div className={styles.favorites}>
            <h1></h1>
                {favorites.length ? (
                    <div className={styles.item}>
                        {favorites.map(cosm => 
                            <CosmeticsItem key={cosm.id} cosm ={cosm} />
                        )}   
                    </div>
                ) : (
                    <div className={styles.content}>
                        <p>    .       -  .</p>
                        <NavLink to='/online-shop'><button className='button'> </button></NavLink> 
                    </div>
                )}   
                {!!favorites.length && <NavLink to='/online-shop'><button className='button'> </button></NavLink>}
        </div>
    )
}

export default Favorites;
